.App {
    text-align: center;
    background: var(--back-color);
    min-height: 100vh;
    padding: 20px;
}

.App h1 {
    font-size: 48px;
    font-weight: bold;
    margin-bottom: 40px;
    background: linear-gradient(135deg, #fff 0%, #21a5b9 50%, #758228 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-shadow: 0 0 40px rgba(33, 165, 185, 0.5);

}

@keyframes titleGlow {
    0%, 100% {
        filter: brightness(1);
    }
    50% {
        filter: brightness(1.2);
    }
}

.search-box {
    margin: 20px auto;
    padding: 28px;
    background: linear-gradient(135deg, rgba(26, 0, 55, 0.3) 0%, rgba(0, 0, 0, 0.5) 100%);
    border-radius: 20px;
    max-width: 800px;
    box-shadow: 0 8px 32px rgba(26, 0, 55, 0.6),
                0 0 20px rgba(33, 165, 185, 0.2),
                inset 0 1px 0 rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(33, 165, 185, 0.2);
    display: flex;
    align-items: center;
    gap: 16px;
}

.search-input {
    flex: 1;
    padding: 14px 18px;
    font-size: 16px;
    color: rgba(255, 255, 255, 0.95);
    background: rgba(26, 0, 55, 0.4);
    border: 2px solid rgba(33, 165, 185, 0.3);
    border-radius: 12px;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    outline: none;
    box-sizing: border-box;
    font-family: inherit;
}

.search-input::placeholder {
    color: rgba(255, 255, 255, 0.5);
}

.search-input:focus {
    border-color: rgba(33, 165, 185, 0.6);
    background: rgba(26, 0, 55, 0.5);
    box-shadow: 0 0 20px rgba(33, 165, 185, 0.3),
                inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.search-results {
    color: rgba(255, 255, 255, 0.9);
    font-size: 16px;
    font-weight: 500;
    white-space: nowrap;
    padding: 0 8px;
}

.filters {
    margin: 20px auto;
    padding: 28px;
    background: linear-gradient(135deg, rgba(26, 0, 55, 0.3) 0%, rgba(0, 0, 0, 0.5) 100%);
    border-radius: 20px;
    max-width: 800px;
    box-shadow: 0 8px 32px rgba(26, 0, 55, 0.6),
                0 0 20px rgba(33, 165, 185, 0.2),
                inset 0 1px 0 rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(33, 165, 185, 0.2);
}

.filters h3 {
    margin-top: 0;
    margin-bottom: 20px;
    color: rgba(255, 255, 255, 0.9);
    text-align: center;
    font-size: 24px;
    font-weight: 600;
    background: transparent;
}

.filter-buttons {
    display: flex;
    gap: 12px;
    flex-wrap: wrap;
    justify-content: center;
}

.filter-btn {
    padding: 12px 24px;
    font-size: 14px;
    font-weight: 500;
    border: 2px solid rgba(33, 165, 185, 0.3);
    border-radius: 30px;
    background: rgba(26, 0, 55, 0.3);
    color: rgba(255, 255, 255, 0.9);
    cursor: pointer;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    letter-spacing: 0.5px;
    position: relative;
    overflow: hidden;
}
/* Форма добавления технологии */
.add-tech-form {
    margin: 20px auto;
    padding: 28px;
    background: linear-gradient(135deg, rgba(26, 0, 55, 0.3) 0%, rgba(0, 0, 0, 0.5) 100%);
    border-radius: 20px;
    max-width: 800px;
    box-shadow: 0 8px 32px rgba(26, 0, 55, 0.6),
                0 0 20px rgba(33, 165, 185, 0.2),
                inset 0 1px 0 rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(33, 165, 185, 0.2);
}

.add-tech-form h3 {
    margin-top: 0;
    margin-bottom: 24px;
    color: rgba(255, 255, 255, 0.9);
    text-align: center;
    font-size: 24px;
    font-weight: 600;
    background: linear-gradient(135deg, #fff 0%, #21a5b9 50%, #758228 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.form-group {
    margin-bottom: 20px;
}

.form-label {
    display: block;
    margin-bottom: 8px;
    color: rgba(255, 255, 255, 0.9);
    font-size: 16px;
    font-weight: 500;
    text-align: left;
}

.form-input,
.form-textarea {
    width: 100%;
    padding: 14px 18px;
    font-size: 16px;
    color: rgba(255, 255, 255, 0.95);
    background: rgba(26, 0, 55, 0.4);
    border: 2px solid rgba(33, 165, 185, 0.3);
    border-radius: 12px;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    outline: none;
    box-sizing: border-box;
    font-family: inherit;
}

.form-input::placeholder,
.form-textarea::placeholder {
    color: rgba(255, 255, 255, 0.5);
}

.form-input:focus,
.form-textarea:focus {
    border-color: rgba(33, 165, 185, 0.6);
    background: rgba(26, 0, 55, 0.5);
    box-shadow: 0 0 20px rgba(33, 165, 185, 0.3),
                inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.form-textarea {
    resize: vertical;
    min-height: 100px;
    line-height: 1.6;
}

.add-tech-btn {
    width: 100%;
    padding: 14px 28px;
    font-size: 16px;
    font-weight: 500;
    border: 2px solid rgba(33, 165, 185, 0.4);
    border-radius: 30px;
    background: linear-gradient(60deg, rgba(33, 165, 185, 0.3) 0%, rgba(33, 165, 185, 0.5) 100%);
    color: white;
    cursor: pointer;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    letter-spacing: 0.5px;
    position: relative;
    overflow: hidden;
    box-shadow: 0 4px 15px rgba(33, 165, 185, 0.3);
    margin-top: 10px;
}

.add-tech-btn::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(60deg, rgba(33, 165, 185, 0.5) 0%, rgba(33, 165, 185, 0.7) 100%);
    opacity: 0;
    transition: opacity 0.3s ease;
    border-radius: 30px;
    z-index: 0;
}

.add-tech-btn:hover {
    color: #ffffff;
    transform: translateY(-2px);
    box-shadow: 0 15px 40px rgba(33, 165, 185, 0.5);
    border-color: rgba(33, 165, 185, 0.6);
}

.add-tech-btn:hover::before {
    opacity: 1;
}

.add-tech-btn:active {
    transform: translateY(0) scale(0.98);
}

.add-tech-btn span {
    position: relative;
    z-index: 1;
}

@media (max-width: 768px) {
    .add-tech-form {
        padding: 20px;
    }

    .add-tech-form h3 {
        font-size: 20px;
    }

    .form-input,
    .form-textarea {
        padding: 12px 16px;
        font-size: 14px;
    }
}

@media (max-width: 480px) {
    .add-tech-form {
        padding: 16px;
    }

    .form-label {
        font-size: 14px;
    }

    .form-input,
    .form-textarea {
        padding: 10px 14px;
        font-size: 14px;
    }

    .add-tech-btn {
        padding: 12px 24px;
        font-size: 14px;
    }
}
.filter-btn::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(33, 165, 185, 0.4);
    opacity: 0;
    transition: opacity 0.3s ease;
    border-radius: 30px;
    z-index: -1;
}

.filter-btn:hover {
    color: #ffffff;
    transform: translateY(-2px);
    box-shadow: 0 4px 15px rgba(33, 165, 185, 0.4);
    border-color: rgba(33, 165, 185, 0.5);
}

.filter-btn:hover::before {
    opacity: 1;
}

.filter-btn.active {
    background: linear-gradient(135deg, rgba(33, 165, 185, 0.4) 0%, rgba(117, 130, 40, 0.4) 100%);
    color: #ffffff;
    border-color: rgba(33, 165, 185, 0.6);
    font-weight: bold;
    box-shadow: 0 4px 15px rgba(33, 165, 185, 0.5);
}

.filter-btn.active::before {
    opacity: 1;
}

.tech-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 24px;
    padding: 20px;
    max-width: 1280px;
    margin: 0 auto;
}

@media (max-width: 768px) {
    .App h1 {
        font-size: 36px;
    }

    .search-box {
        padding: 20px;
        flex-direction: column;
        align-items: stretch;
    }

    .search-results {
        text-align: center;
        padding: 8px 0 0 0;
    }

    .filters {
        padding: 20px;
    }

    .filters h3 {
        font-size: 20px;
    }

    .filter-btn {
        padding: 10px 20px;
        font-size: 13px;
    }
}

@media (max-width: 480px) {
    .App h1 {
        font-size: 28px;
    }

    .search-box {
        padding: 16px;
    }

    .search-input {
        padding: 12px 16px;
        font-size: 14px;
    }

    .search-results {
        font-size: 14px;
    }

    .filters {
        padding: 16px;
    }

    .filter-buttons {
        gap: 8px;
    }

    .filter-btn {
        padding: 8px 16px;
        font-size: 12px;
    }
}
