.modal-background {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  backdrop-filter: blur(4px);
}

.modal-window {
  background: linear-gradient(135deg, rgba(26, 0, 55, 0.95) 0%, rgba(0, 0, 0, 0.95) 100%);
  border-radius: 20px;
  padding: 0;
  width: 90%;
  max-width: 500px;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 0 8px 32px rgba(26, 0, 55, 0.8),
              0 0 40px rgba(33, 165, 185, 0.4),
              inset 0 1px 0 rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(33, 165, 185, 0.3);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 24px;
  border-bottom: 1px solid rgba(33, 165, 185, 0.2);
}

.modal-header h2 {
  margin: 0;
  font-size: 1.5rem;
  color: rgba(255, 255, 255, 0.9);
  background: linear-gradient(135deg, #fff 0%, #21a5b9 50%, #758228 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.close-button {
  background: none;
  border: none;
  font-size: 32px;
  cursor: pointer;
  color: rgba(255, 255, 255, 0.7);
  padding: 5px 10px;
  line-height: 1;
  transition: all 0.3s ease;
  border-radius: 8px;
}

.close-button:hover {
  color: rgba(255, 255, 255, 1);
  background: rgba(255, 255, 255, 0.1);

}

.modal-content {
  padding: 24px;
  color: rgba(255, 255, 255, 0.9);
}

.modal-content p {
  margin: 12px 0;
  line-height: 1.6;
}

.modal-content button {
  margin-top: 16px;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: 500;
  border: 2px solid rgba(33, 165, 185, 0.4);
  border-radius: 30px;
  background: linear-gradient(60deg, rgba(33, 165, 185, 0.3) 0%, rgba(33, 165, 185, 0.5) 100%);
  color: white;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  letter-spacing: 0.5px;
}

.modal-content button:hover {
  transform: translateY(-2px);
  box-shadow: 0 15px 40px rgba(33, 165, 185, 0.5);
  border-color: rgba(33, 165, 185, 0.6);
}

.modal-content button:active {
  transform: translateY(0) scale(0.98);
}

.modal-content textarea {
  width: 100%;
  padding: 14px 18px;
  font-size: 16px;
  color: rgba(255, 255, 255, 0.95);
  background: rgba(26, 0, 55, 0.4);
  border: 2px solid rgba(33, 165, 185, 0.3);
  border-radius: 12px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  outline: none;
  box-sizing: border-box;
  font-family: inherit;
  resize: vertical;
  min-height: 150px;
  margin-top: 12px;
}

.modal-content textarea::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.modal-content textarea:focus {
  border-color: rgba(33, 165, 185, 0.6);
  background: rgba(26, 0, 55, 0.5);
  box-shadow: 0 0 20px rgba(33, 165, 185, 0.3),
              inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.modal-content label {
  display: block;
  margin-bottom: 8px;
  color: rgba(255, 255, 255, 0.9);
  font-size: 16px;
  font-weight: 500;
}

.modal-description {
  padding: 12px;
  border-radius: 8px;
  background: rgba(33, 165, 185, 0.1);
  border-left: 3px solid rgba(33, 165, 185, 0.5);
  margin-bottom: 20px;
  color: rgba(255, 255, 255, 0.85);
  line-height: 1.6;
}

.notes-section {
  margin-top: 20px;
}

.notes-hint {
  margin-top: 8px;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.6);
  font-style: italic;
}

.modal-buttons {
  display: flex;
  gap: 12px;
  margin-top: 20px;
}

.modal-buttons button {
  flex: 1;
  margin-top: 0;
}

.cancel-btn {
  background: linear-gradient(60deg, rgba(255, 107, 107, 0.3) 0%, rgba(255, 107, 107, 0.5) 100%) !important;
  border-color: rgba(255, 107, 107, 0.4) !important;
}

.cancel-btn:hover {
  box-shadow: 0 15px 40px rgba(255, 107, 107, 0.5) !important;
  border-color: rgba(255, 107, 107, 0.6) !important;
}

@media (max-width: 768px) {
  .modal-window {
    width: 95%;
    max-height: 90vh;
  }

  .modal-header {
    padding: 16px 20px;
  }

  .modal-header h2 {
    font-size: 1.3rem;
  }

  .modal-content {
    padding: 20px;
  }
}

